import React, { useState, useEffect } from 'react';
import { hot } from 'react-hot-loader';
import {
  Container, Header, Button, Segment, List,
} from 'semantic-ui-react';
import Number from 'react-number-format';
import { Contract, utils, ethers } from 'ethers';
import moment from 'moment';
import { toast } from 'react-toastify';
import Page from '../components/Page';
import EtherscanLink from '../components/Link/EtherscanLink';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';

import {
  FancyNftHeroesS1Address,
  LotteryPoolAddress,
  nullAddress,
} from '../constants';
import LotteryPoolAbi from '../abis/LotteryPoolAbi.json';



function LotteryPage() {
  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const getContract = (withSigner = false) => {
    let provider = library;
    if (withSigner) provider = library.getSigner();
    return new Contract(LotteryPoolAddress, LotteryPoolAbi, provider);
  }
  // address
  const [owner, setOwner] = useState(nullAddress);
  const [lotteryPool, setLotteryPool] = useState('0');
  const [lotteryIsActivated, setLotteryIsActivated] = useState(false);
  const [nextLotteryNumber, setNextLotteryNumber] = useState(0);
  const [lotteryInterval, setNextLotteryInterval] = useState(0);
  const [nextLotteryTime, setNextLotteryTime] = useState(0);
  const [lotteryMaxAmount, setNextLotteryMaxAmount] = useState('0');
  const [nextLotteryAmount, setNextLotteryAmount] = useState('0');
  const [won, setWon] = useState('0');
  const [wonTotal, setWonTotal] = useState('0');
  const [lotteryWinners, setLotteryWinners] = useState('0');
  const [sharesTotal, setSharesTotal] = useState('0');
  const [shares, setShares] = useState([]);

  const [logs, setLogs] = useState([]);

  // update
  const [initDone, setInitDone] = useState(false);
  const [updating, setUpdating] = useState(false);
  const update = async () => {
    if (updating) return;
    setUpdating(true);
    try {
      const contract = getContract();

      // address
      const owner = await contract.owner();
      setOwner(owner);

      let lotteryPool = await library.getBalance(LotteryPoolAddress);
      lotteryPool = utils.formatEther(lotteryPool);
      setLotteryPool(lotteryPool);
      let lotteryIsActivated = await contract.lotteryIsActivated();
      setLotteryIsActivated(lotteryIsActivated);
      let nextLotteryNumber = await contract.nextLotteryNumber();
      setNextLotteryNumber(nextLotteryNumber);
      let lotteryInterval = await contract.lotteryInterval();
      setNextLotteryInterval(lotteryInterval);
      let nextLotteryTime = await contract.nextLotteryTime();
      setNextLotteryTime(nextLotteryTime);
      let lotteryMaxAmount = await contract.lotteryMaxAmount();
      setNextLotteryMaxAmount(utils.formatEther(lotteryMaxAmount));
      let nextLotteryAmount = await contract.nextLotteryAmount();
      setNextLotteryAmount(utils.formatEther(nextLotteryAmount));

      let wonTotal = await contract.wonTotal();
      setWonTotal(utils.formatEther(wonTotal));

      let sharesTotal = await contract.sharesTotal();
      setSharesTotal(utils.formatEther(sharesTotal));
      let lotteryWinners = await contract.lotteryWinners();
      setLotteryWinners(lotteryWinners.toNumber());
      const shares = [];
      for (let i = 0; i < lotteryWinners; i++) {
        const share = await contract.shares(i);
        shares.push(share.toNumber());
      }
      setShares(shares);

      if (account) {
        let won = await contract.won(account);
        setWon(utils.formatEther(won));
      } else {
        setWon('0');
      }

    } catch (error) { console.error(error); }
    setInitDone(true);
    setUpdating(false);
  }

  const [blockNumber, setBlockNumber] = useState(-1);
  const [time, setTime] = useState(0);
  useEffect(() => {
    if (library) {
      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
      const contract = getContract();
      contract.queryFilter('Winner').then((data) => {
        setLogs(data);
      });
    }

    setTime(Date.now());
    const intervalId = setInterval(() => {
      setTime(Date.now());
    }, 1 * 1000);

    return () => {
      clearInterval(intervalId);
    };
  }, []);
  useEffect(() => {
    if (library) {
      if (logs.length === 0) {
        const contract = getContract();
        contract.queryFilter('Winner').then((data) => {
          setLogs(data);
        });
      }

      library.on('block', (blockNumber) => {
        setBlockNumber(blockNumber);
        update();
      });
    }
  }, [library]);

  useEffect(() => {
  }, [time]);
  // update end


  // checks
  const isOwner = () => {
    if (account === owner) {
      return true;
    }
    return false;
  }
  const onlyOwner = () => {
    if (!isOwner()) {
      toast.error('Not Owner');
      throw new Error('Not Owner');
    }
  }
  // checks end


  const doTx = async (promiseTx) => {
    try {
      const tx = await promiseTx;
      if (!tx) return;
      toast.info('Pending tx doTx', tx.hash);
      await tx.wait(1);
      toast.success('Done tx doTx', tx.hash);
    } catch (error) {
      console.error(error);
      toast.error('Error doTx');
    }
  }

  const runLottery = async () => {
    const contract = getContract(true);
    const estimation = await contract.estimateGas.startLottery();
    await doTx(contract.startLottery({ gasLimit: estimation.mul(120).div(100) }));
  }
  //setLotteryMaxAmount


  return (
    <Page title={'Lottery'}>
      <Container>
        <br />
        <Segment>
          <Header as='h1'>
            <Header.Content>
              Lottery
              <Header.Subheader>
                <div>Smart Contract: <EtherscanLink address={LotteryPoolAddress}>{LotteryPoolAddress}</EtherscanLink></div>
                <div>Owner: <EtherscanLink address={owner}>{owner}</EtherscanLink></div>
                <div>Last Block: <Number value={blockNumber}
                  displayType='text' thousandSeparator />
                </div>
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Segment>

        <Segment>
          <div>Status: {lotteryIsActivated ? 'Is Active' : 'Not Active'}</div>
          <div>Pool: <EtherscanLink address={LotteryPoolAddress}><Number value={lotteryPool} suffix=' BNB' displayType='text' thousandSeparator /></EtherscanLink></div>
          <div>Won Total: <Number value={wonTotal} suffix=' BNB' displayType='text' thousandSeparator /></div>
          <div>Max Lottery Amount: <Number value={lotteryMaxAmount} suffix=' BNB' displayType='text' thousandSeparator /></div>
          <div>Winners: {lotteryWinners}</div>
          <div>Winners Shares: ({shares.join('%, ')}%)</div>
          <div>Interval: {lotteryInterval} seconds</div>
          <div>Next Number: {nextLotteryNumber}</div>
          <div>Next Time: {nextLotteryTime} ({moment(nextLotteryTime, 'X').fromNow()})</div>
          <div>Next Amount: <Number value={nextLotteryAmount} suffix=' BNB' displayType='text' thousandSeparator /></div>
        </Segment>

        <Segment>
          <div>Address: <EtherscanLink address={account}>{account}</EtherscanLink></div>
          <div>Won Total: <Number value={won} suffix=' BNB' displayType='text' thousandSeparator /></div>

          <Button primary onClick={runLottery}>Run Lottery</Button>
        </Segment>


        <Segment>
          <div>Winners:</div>
          <List>
            {logs.map((item, index) => {
              let amount = utils.formatEther(item.args.amount);
              let tokenId = item.args.tokenId.toString();
              let tokenIdLink = `${FancyNftHeroesS1Address}?a=${tokenId}#inventory`;
              return (<List.Item key={index}>
                <Segment>
                  <div>
                    Lottery: {item.args.lottery.toString()}{' '}
                    Place: {item.args.place.toString()}{' '}
                    Amount: <Number value={amount} suffix=' BNB' displayType='text' thousandSeparator />{' '}
                  </div>
                  <div>
                    Winner tokenId: <EtherscanLink token={tokenIdLink}>{tokenId}</EtherscanLink>{' '}
                    Winner Address: <EtherscanLink address={item.args.account}>{item.args.account.substring(0, 6)}</EtherscanLink>{' '}
                  </div>

                  <div>
                    BlockNumber: {item.blockNumber},{' '}
                    TX: <EtherscanLink tx={item.transactionHash}>{item.transactionHash.substring(0, 6)}</EtherscanLink>
                  </div>
                </Segment>
              </List.Item>);
            })}
          </List>
        </Segment>
      </Container>
    </Page>
  );
};

export default hot(module)(LotteryPage);
